nav.util {
    a {
        color: palette(primary);
        display: inline-block;
        font-size: 12px;
        margin-bottom: 20px;
        &:hover {
            color: $white;
        }
    }
    .selected a {
        color: $white;
    }
    @include breakpoint(1024) {
        display: none;
    }
}

nav.main {
    a {
        color: $white;
        display: inline-block;
        font-size: 19px;
        padding-bottom: 10px;
        font-weight: $font-weight--medium;
        &:hover {
            color: palette(primary);
        }
    }
    .selected {
        border-bottom: 2px solid palette(primary);
    }
    @include breakpoint(1024) {
        display: none;
    }
}

nav.bun {
    width: 40px;
    height: 22px;
    position: relative;
    cursor: pointer;
    display:none;
    @include breakpoint(1024) {
        display: block;
    }
    .ingredient {
        height: 2px;
        border-radius: 5px;
        background: $white;
        position: relative;
        @include transition(all, ease, 0.3s);
    }
    .salad {
        top: 0px;
    }
    .cheese {
        top: 8px;
    }
    .meat {
        top: 16px;
    }
    &.active {
        .ingredient {
            background: palette(accent);
        }
        .salad {
            top: 14px;
            @include rotate(45);
        }
        .cheese {
            top: 8px;
            opacity: 0;
        }
        .meat {
            top: 10px;
            @include rotate(-45);
        }
    }
}

.mobile-nav-interface {
    @include position(0px, auto, 0px, auto);
    @include transition(all, ease, 0.8s);
    opacity: 0;
    z-index: -1;
    position: fixed;
    width:100%; height:100%;
    background: rgba($black, 0.9);
    display: flex;
    left:-2000px;
    &.active {    
        opacity: 1;
        z-index: 9;
        left:0px;
    }
    img{
        margin-bottom:20px;
    }
    .main{
        font-family: $font-family--heading;
        font-weight: $font-weight--medium;
        margin-bottom:30px;
        font-size:25px;
        line-height: 30px;
        padding:0px 10px;
        color:$grey;
        a{
            color:$white;
        }
    }
    .util{
        font-size:15px;
        line-height: 20px;;
        padding:0px 10px;
    }
    @include breakpoint(600){
        a{
            display:block;
            margin-bottom: 10px;
        }
        span{
            display: none;
        }
    }
}

.mobile-nav-container {
    position: relative;
    z-index: 10;
}

.lock {
    overflow: hidden;
}