body{
    font-family: $font-family--primary;
    font-size:18px;
    line-height: 18px;
    font-weight: 300;
    background:$black;
}


h1, h2, h3, h4, h5, h6{
    font-family: $font-family--heading;
    font-weight: normal;
}


h1{
    font-size: 55px;
    line-height: 55px;
    font-weight: $font-weight--bold;
    @include breakpoint(600){
        font-size: 44px;
        line-height: 42px;
    }
}

h2{
    font-size: 26px;
    line-height: 26px;
    font-weight: $font-weight--bold;
}

h3{
    font-size: 20px;
    line-height: 20px;
    font-weight: $font-weight--bold;
}

h4{
    font-size: 13px;
    line-height: 13px;
    font-weight: $font-weight--bold;
}

p{
    font-size:18px;
    line-height: 25px;
}
a{
    color:palette(accent);
    text-decoration: none;
    @include transition(all, ease-in-out, 0.3s);
}

  