//mixins
// breakpoints specific
// usage: @include breakpoint(940){ width:100%; }
@mixin breakpoint($size) {
    @media only screen and (max-width: $size + px) {
        @content;
    }
}


// usage: @include transition(all, 0.3s, ease-in)
@mixin transition($property:all, $duration:0.2s, $timing:ease) {
    -webkit-transition: $property $duration $timing;
    -moz-transition: $property $duration $timing;
    -o-transition: $property $duration $timing;
    transition: $property $duration $timing;
}

// Call gradient scrim
// usage: @include scrim(color, color)
@mixin scrim($colorbottom, $colortop, $direction) {
    background: -moz-linear-gradient($direction, rgba($colorbottom, 0) 0%, rgba($colortop, 1) 100%);
    background: -webkit-linear-gradient($direction, rgba($colorbottom, 0) 0%, rgba($colortop, 1) 100%);
    background: linear-gradient($direction, rgba($colorbottom, 0) 0%, rgba($colortop, 1) 100%);
}

// Call gradient whatever
// usage: @include gradient(color, color)
@mixin gradient($colorbottom, $colortop, $direction) {
    background: -moz-linear-gradient($direction, rgba($colorbottom, 1) 0%, rgba($colortop, 1) 100%);
    background: -webkit-linear-gradient($direction, rgba($colorbottom, 1) 0%, rgba($colortop, 1) 100%);
    background: linear-gradient($direction, rgba($colorbottom, 0) 0%, rgba($colortop, 1) 100%);
}

// usage: @include position(pos, pos, pos, pos)
@mixin position($top, $bottom, $left, $right) {
    position: absolute;
    top: $top;
    bottom: $bottom;
    left: $left;
    right: $right;
}


// usage @include hexagon(width, color)
@mixin hexagon($shapeW, $shapeC) {
    width: $shapeW;
    height: ($shapeW / 2 + $shapeW / 20);
    background: $shapeC;
    position: relative;
    &:before {
        content: "";
        position: absolute;
        top: - ($shapeW / 4);
        left: 0;
        width: 0;
        height: 0;
        border-left: ($shapeW / 2) solid transparent;
        border-right: ($shapeW / 2) solid transparent;
        border-bottom: ($shapeW / 4) solid $shapeC;
    }
    &:after {
        content: "";
        position: absolute;
        bottom: - ($shapeW / 4);
        left: 0;
        width: 0;
        height: 0;
        border-left: ($shapeW / 2) solid transparent;
        border-right: ($shapeW / 2) solid transparent;
        border-top: ($shapeW / 4) solid $shapeC;
    }
}

//transform

@mixin rotate($amount){
    transform: rotate($amount + deg);
    -webkit-transform: rotate($amount + deg);
    -moz-transform: rotate($amount + deg);
    -ms-transform: rotate($amount + deg);
}

// Call the color palette modifiers
@function palette($palette, $shade: 'base') {
    @return map-get(map-get($palettes, $palette), $shade)
}