.form{
    label{
        display: block;
        margin-bottom: 10px;
        text-transform: uppercase;
        font-weight: $font-weight--bold;
        font-family: $font-family--heading;
        font-size:16px;
    }
    input[type=text], input[type=email], textarea, select{
        background:$lgrey;
        padding:10px;
        width:100%;
        border-radius: 3px;
        border:0px;
        margin-bottom:20px;
    } 

    input{
        &.btn{
            border:0px;
        }
    }
}