// patterns

// Font Stack

$font-family--heading              :   "jaf-domus-titling-web", sans-serif;
$font-family--primary              :   "museo-sans", sans-serif;
$font-family--custom               :   "ff-market-web";

// Font Weights

$font-weight--light          :   300;
$font-weight--regular        :   400;
$font-weight--medium         :   500;
$font-weight--bold           :   600;

// Descriptive Base Colors
 
$white                :   #ffffff;
$off-white            :   #fefefe;
$black                :   #000;
$grey                 :   #555555;
$lgrey                :   #ebebeb;
$text                 :   #343434;

// Color Usage

$primary              :   #cb2e38;
$secondary            :   #000;
$tertiary             :   #ebebeb;
$accent               :   #cb2e38;

$border-radius        :   2px;

//button style

$btn-width            :   auto;
$pill                 :   50px;
$square               :   2px;
$normal               :   5px;

//call global

$btnstyle             :   $square;


// Color Palette modifiers

$palettes: (
    primary: (
        x-light     : lighten($primary   , 16%),
        light       : lighten($primary   , 8%),
        base        : $primary  ,
        dark        : darken($primary  , 6%),
        x-dark      : darken($primary  , 12%)
        ),
    secondary: (
        x-light     : lighten($secondary, 16%),
        light       : lighten($secondary, 8%),
        base        : $secondary,
        dark        : darken($secondary, 6%),
        x-dark      : darken($secondary, 12%)
        ),
     tertiary: (
        xx-light    : lighten($tertiary , 24%),
        x-light     : lighten($tertiary , 16%),
        light       : lighten($tertiary , 8%),
        base        : $tertiary,
        dark        : darken($tertiary, 6%),
        x-dark      : darken($tertiary, 12%)
        ),
     accent: (
        x-light     : lighten($accent , 16%),
        light       : lighten($accent , 8%),
        base        : $accent,
        dark        : darken($accent, 6%),
        x-dark      : darken($accent, 12%)
        )
    );
