.fixed {
    position: fixed;
    top: 0px;
    left: 0px;
    width: 100%;
    @include breakpoint(1023){
        position: absolute;
    }
}

.lock .logo{
    display:none;
}

header {
    z-index: 2;
    .logo{
        position:absolute;
        top:12px;
        z-index:99;
        @include breakpoint(1024){
            top:-8px;
            width:200px;
            img{
                max-width:100%;
            }
        }
        a{
            display:block;
        }
    }
    &.main {
        background: rgba($black, 0.9);
        @include breakpoint(1023){
            background: $black;
        }
        font-family: $font-family--heading;
        padding:20px 0px 30px 0px;
        ul {
            list-style: none;
            padding: 0px;
            margin: 0px;
            li {
                float: left;
                margin-left:40px;
                &:first-child{
                    margin-left: 0px; 
                }
                a{
                    text-decoration: none;
                }
            }
        }
    }
}

.address{
    font-family: $font-family--heading;
    line-height: 1.5;
}