.item {
    position: relative;
    margin: 5px 5px 30px 5px;
    img {
        width: 100%;
        display: block;
    }
    .fat {
        @include transition(1s, all, ease-out);
        position: absolute;
        top: 0px;
        left: 0px;
        width: 100%;
        height: 100%;
        opacity: 0;
        img {
            height: 100%;
        }
    }
    .content {
        @include position(0px, 0px, 0px, 0px);
        @include transition(0.3s, all, ease-out);
        background-color: rgba($black, 0.1);
        h3 {
            margin: 0px;
        }
        .hex-pos {
            @include position(auto, -10px, auto, auto);
            width: 100%;
        }
        .hexagon {
            @include hexagon(69px, rgba(palette(accent), 0));
            margin: auto;
            .mini-hexagon {
                @include hexagon(56px, rgba(palette(accent), 1));
                @include position(4px, auto, 6px, auto);
                background: palette(accent) url('../img/icon-pulse.png') center center no-repeat;
            }
        }
        .label {
            @include position(auto,
            0px,
            0px,
            auto);
            width: 100%;
            padding-bottom: 60px;
            @include transition(0.3s,
            all,
            ease-out);
        }
    }
    .results & {
        margin: 0px;
        margin-bottom: 40px;
        &:hover {
            .fat {
                opacity: 1;
            }
        }
    }
}

.results .col-3 {
    margin-bottom: 20px;
}

.results {
    .desc {
        margin-top:30px;
        text-align: center;
        p {
            font-size: 17px;
        }
    }
}