.strip{
    padding:40px 0px;
    &.none{
        padding:0px;
    }
    &-black{
        background:$black;
        color:$white;
    }
    &-lgrey{
        background:$lgrey;
    }
    &-white{
        background:$white;
        color:$text;
    } 
    &-accent{
        background:palette(accent);
        color:$white;
    } 
}