.banner{
    position: relative;
    background:url('../img/banner-main.jpg') center center no-repeat;
    background-size:cover;
    height:100vh;
    @include breakpoint(640){
        height:80vh;
    }
    .content{
        @include position(0px, 0px, 0px, 0px);
        padding-bottom: 80px;
        h1{
            margin:0px;
        }
        h2{
            font-size:30px;
            margin:0px;
            font-weight: $font-weight--light;
        }
    }
}

