.img-item {
    display: block;
    position: relative;
    border-bottom: 4px solid rgba(255, 255, 255, 0.05);
    img {
        width: 100%;
        display: block;
    }
    .content {
        @include position(auto, 0px, 0px, auto);
        width: 100%;
        padding: 15px;
        h3 {
            margin: 0px;
            //margin-bottom: 5px;
        }
    }
}

.item-data {
    position: relative;
    top:-4px;
    h4, h2 {
        margin: 0px;
    }
    ul{
        list-style:none;
        margin:0px; padding:0px;
        text-transform: uppercase;
        font-size: 12px;
        font-weight: $font-weight--medium;
    }
}