.accordion{
    .question{
        font-size:22px;
        padding:20px; 
        padding-right:40px;
        border-bottom:2px solid palette(accent);
        font-weight: $font-weight--medium;
        position:relative;
        line-height: 28px;
        cursor: pointer;
        i{
            position:absolute;
            right:20px;
            top:24px;
            @include transition(0.3s, all, ease-in);
            &.switch{
                @include rotate(180);
                color:palette(accent);
            }
        }
    }
    .answer{
        display: none;
    }
}