//framework

* {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
}

*:before,
*:after {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
}

.clearfix:before,
.clearfix:after {
    content: "";
    display: table;
}

.clearfix:after {
    clear: both;
}

.clearfix {
    zoom: 1;
}

.row {
    margin-left: -10px;
    margin-right: -10px;
    position: relative;
}


div[class*=col-],
section[class*=col-],
li[class*=col-],
a[class*=col-] {
    float: left;
    position: relative;
    padding: 0px 10px;
}

div[class*=span-],
section[class*=span-],
li[class*=span-],
a[class*=span-] {
    float: left;
    position: relative;
    padding: 0px 10px;
}

.base {
    margin: auto;
    position: relative;
    width: 95%;
    max-width: 1170px;
    @include breakpoint(750){
        width: 90%;
    }
    @include breakpoint(480){
        &.full-width{
            width:100% !important;
        }
    }
}

.no-gutter > [class*='col-'] {
    padding-right:0;
    padding-left:0;
}

@for $i from 1 through 12 {
    $j: (100/12 * $i);
    .col-#{$i} {
        width: unquote($j + '%');
        @include breakpoint(480){
            width:100%;
        }
    }
}

.col-3{
    @include breakpoint(767){
        width:50%;
        margin-bottom:20px;
    }
    @include breakpoint(480){
        width:100%;
    }
}

.col-4{
    @include breakpoint(767){
        width:50%;
        margin-bottom:20px;
    }
    @include breakpoint(480){
        width:100%;
    }
}

.col-6{
    @include breakpoint(480){
        width:100%;
        margin-bottom:20px;
    }
}