//helpers
// Text alignment & style/size
.text {
    &-center {
        text-align: center;
    }
    &-right {
        text-align: right;
    }
    &-left {
        text-align: left;
    }
    &-uppercase {
        text-transform: uppercase;
    }
    &-lead {
        font-size: 27px;
        line-height: 27px;
    }
    &-sml {
        font-size: 14px;
        line-height: 14px;
    }
    &-custom-font {
        font-family: $font-family--custom;
        font-weight: 400;
        line-height: 1.3;
    }
    &-description {
        font-size: 13px;
        font-family: $font-family--heading;
        font-weight: $font-weight--medium;
    }
    &-italic{
        font-style: italic;
    }
    &-bold{
        font-weight: $font-weight--medium;
    }
}

.scrim {
    @include scrim(black, black, top);
}

.valign {
    display: flex;
    align-items: center;
    justify-content: center;
    .valign-bottom{
        align-self: flex-end;
        width:100%;
    }
    .valign-center{
        align-self: center;
        width:100%;
    }
}

// Responsive Images 
.img-responsive {
    max-width: 100%;
    height: auto !important;
    display: inline-block;
}

// Text Colours
.color {
    &-primary {
        color: palette(primary) !important;
    }
    &-secondary {
        color: palette(secondary) !important;
    }
    &-accent {
        color: palette(accent) !important;
    }
    &-tertiary {
        color: palette(tertiary) !important;
    }
    &-white {
        color: $white !important;
    }
    &-grey {
        color: $grey !important;
    }
    &-lgrey {
        color: $lgrey !important;
    }
}

@for $i from 1 through 10 {
    $j: (100/10 * $i);
    .mt-#{$i} {
        margin-top: unquote($j + 'px') !important;
    }
}

@for $i from 1 through 10 {
    $j: (100/10 * $i);
    .mb-#{$i} {
        margin-bottom: unquote($j + 'px') !important;
    }
}

@for $i from 1 through 10 {
    $j: (100/10 * $i);
    .pt-#{$i} {
        padding-top: unquote($j + 'px') !important;
    }
}

@for $i from 1 through 10 {
    $j: (100/10 * $i);
    .pb-#{$i} {
        padding-bottom: unquote($j + 'px') !important;
    }
}

@for $i from 1 through 10 {
    $j: (100/10 * $i);
    .p-#{$i} {
        padding: unquote($j + 'px') !important;
    }
}

@for $i from 1 through 12 {
    $j: (100/12 * $i);
    .wide-#{$i} {
        width: unquote($j + '%');
        @include breakpoint(768){
            width:100%;
        }
    }
}

.pull-right {
    float: right !important;
}

.pull-left {
    float: left !important;
}

.pull-center {
    float: none !important;
    margin: auto;
}

.hide-mobile{
    @include breakpoint(480){
        display:none;
    }
}

.hide-tablet{
    @include breakpoint(768){
        display:none;
    }
}

.show-mobile{
    display:none;
    @include breakpoint(480){
        display:block;
    }
}

.show-tablet{
    display:none;
    @include breakpoint(768){
        display:block;
    }
}